/* General Styles */
.App {
  text-align: center;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Header Styles */
.header {
  background-color: #000;
  color: #fff;
  margin: 0;
  padding: 1rem;
}

/* Body Styles */
.body {
  margin: 0;
  padding: 0;
  min-height: 100vh; /* Use min-height instead of height */
}

/* Slideshow Component */
.slideshow {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

/* Container Styles for Flexbox Layout */
.container {
  display: flex;
  height: calc(100vh - 55px);
  width: 100%;
  margin: 0;
}

.left-component, .right-component {
  flex: 1;
  /* padding: 20px; */
  box-sizing: border-box;
  overflow: hidden;
}

.left-component {
  background-color: #eed6d6;
}

.right-component {
  background-color: #b2ebf2;
}

/* Additional Styling */
.left-component h2, .right-component h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.left-component p, .right-component p {
  font-size: 1em;
}

/* Footer Styles */
footer {
  background-color: black;
  color: white;
  height: 55px;
  padding: 1rem;
  text-align: center;
  position: relative; /* Changed from absolute to relative */
  width: 100%;
}

/* Responsive Design for Mobile Devices */
@media (max-width: 768px) {
  /* Container flex-direction for mobile screens */
  .container {
    flex-direction: column;
    height: auto; /* Adjust height to content */
  }

  .left-component, .right-component {
    padding: 15px; /* Adjust padding for mobile */
  }

  /* Footer adjustment for mobile */
  footer {
    position: relative;
  }
}
